<template>
  <div>
    <b-sidebar v-model="showpopup" class="commentSidebar" right no-header backdrop shadow>
      <b-row no-gutters class="commentSidebar-header px-3 pt-2">
        <b-col cols="12">販売未収条件 明細コメント</b-col>
        <b-col cols="8">
          <b-button tag="a" variant="none">@({{mainItemNumber}})</b-button>
        </b-col>
        <b-col cols="4" class="text-right">
          <i class="fas fa-arrow-right" @click="showpopup = false"></i>
        </b-col>
      </b-row>

      <b-row no-gutters class="commentSidebar-content mx-3 bg-light">
        <textarea rows="6" class="w-100" v-model="commentValue"></textarea>
        <b-col cols="12" class="mt-2 text-dark">
          <b-form-checkbox
                  id="isSameStoreName"
                  name="isSameStoreName"
                  v-model="isSameStoreName"
                  value="true"
                  class="ml-2"
                  unchecked-value="false"
                  plain
                  size="lg"
          >同じ店舗グループ名に同じコメント</b-form-checkbox>
        </b-col>

        <b-col cols="12" class="mt-2 text-dark">
          <b-form-checkbox
                  id="isSameShain"
                  name="isSameShain"
                  v-model="isSameShain"
                  value="true"
                  class="ml-2"
                  unchecked-value="false"
                  size="lg"
                  plain
          >同じ担当者の明細に同じコメント</b-form-checkbox>
        </b-col>
        <b-col cols="12" class="mt-2 text-dark">
          <b-form-checkbox
                  id="isSameShainExclamation"
                  name="isSameShainExclamation"
                  v-model="isSameShainExclamation"
                  value="true"
                  unchecked-value="false"
                  class="ml-2"
                  size="lg"
                  plain
          >同じ担当者のに同じコメント</b-form-checkbox>
        </b-col>
      </b-row>
      <b-row no-gutters class="commentSidebar_handle px-3 mt-2">
        <b-col cols="6" class="pr-1">
          <b-button variant="none" class="w-100 text-light" @click="showCommentHistory">過去のコメントを再利用ボタン</b-button>
        </b-col>
        <b-col cols="6" class="pl-2">
          <b-button variant="none" class="w-100 text-light" @click="addComment">入力するボタン</b-button>
        </b-col>
      </b-row>
      <b-row no-gutters class="commentSidebar_user px-3 mt-2">
        <b-col cols="12" class="commentSidebar_user_item mb-2" v-for="(comment,index) in oldComments" :key="index">
          <b-row no-gutters class="py-1">
            <b-col cols="6" class="pl-1">
              <i class="fas fa-user-alt"></i> {{comment.shainNm}}
            </b-col>
            <b-col cols="6">[{{ $moment(comment.createdAt).format('YYYY/MM/DD hh:mm') }}]</b-col>
          </b-row>
          <b-row no-gutters class="py-1">
            <b-col cols="12" class="pl-1">{{comment.comment}}</b-col>
            <b-col cols="12" class="pr-2">
              <i class="fas fa-trash-alt float-right" @click="deleteComment(comment.cd)"></i>
            </b-col>
          </b-row>
        </b-col>
      </b-row>
    </b-sidebar>
    <CommentHistoryDialog v-if="isShowCommentHistory" v-model="isShowCommentHistory" @onSelectedComment="onSelectedComment($event)"/>
  </div>
</template>
<script>
  import CommentService from "@/services/CommentService";
  import CommentHistoryDialog from "./CommentHistoryDialog";
  export default {
    name: "CommentSlideDialog",
    components: {CommentHistoryDialog},
    data() {
      return {
        isSameStoreName: false,
        isSameShain: false,
        isSameShainExclamation: false,
        isShowCommentHistory: false,
        showpopup: this.value,
        commentValue: "",
        oldComments: []
      };
    },
    props: {
      mainItemId: {
        type: Number
      },
      mainItemNumber: {
        type: [String,Number]
      },
      value: {
        type: Boolean
      },
      initComment: {
        type: String
      }
    },
    methods: {
      showCommentHistory() {
        this.isShowCommentHistory = true;
      },
      onSelectedComment(comment) {
        this.isShowCommentHistory = false;
        this.commentValue = comment;
      },
      addComment() {
          let newComment = {
            itemCode: this.mainItemNumber,
            comment: this.commentValue,
            isSameStoreName: this.isSameStoreName,
            isSameShain: this.isSameShain,
            isSameShainExclamation: this.isSameShainExclamation
          }
          this.$emit("onAddedComment", newComment);
          this.showpopup = false;
      },
      deleteComment(commentCd) {
         if (commentCd) {
           if(confirm("Do you really want to delete?")) {
             CommentService.deleteComment(commentCd).then(res => {
               this.oldComments = this.oldComments.filter( function(comment) {
                 return comment.cd !== commentCd;
               });
             }).catch(err => {
               console.log(err);
             });
           }
         }
      }
    },
    watch: {
      value() {
        this.showpopup = this.value;
      },
      showpopup() {
        this.$emit("input", this.showpopup);
      }
    },
    computed: {
    },
    created() {
      if(this.initComment){this.commentValue = this.initComment}
      if (this.mainItemId) {
        CommentService.getCommentsOfAnItem(this.mainItemId).then(res => {
           this.oldComments.push(...res.data);
        }).catch(err => {
          console.log(err);
        });
      }
    }
  };
</script>
<style lang="scss" scoped>
  p {
    margin: 0;
    font-size: 0.7rem;
    font-weight: 500;
  }

  .input_filter {
    width: 100%;
    font-size: 0.7rem;
    font-weight: 700;
    border: 1px solid grey;
    border-radius: 0;
    padding: 0 0.4rem;
  }

  .table th, .table td {
    border-top: 1px solid grey;
    font-size: 1rem;
  }

  table tr{
    cursor: pointer;
  }

  .commentSidebar_user {
    display: block;
  }
</style>