import moment from 'vue-moment';
import SupplierService from "@/services/SupplierService";

const state = {
    requestList: null
};

const actions = {
  getRequestList({commit},payload){
    SupplierService.requestList(payload).then(res => {
       commit("SET_REQUESTLIST",res.data.content)
    }).catch(err => {
      console.log(err);
    })
  }
};
const getters = {

};
const mutations = {
  SET_REQUESTLIST(state,payload){
    state.requestList = payload;
  }
};
export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
}