<template>
  <ValidationObserver v-slot="invalid">
    <b-container fluid no-gutters class="table-container">
        <table :class="['table b-table customScrollBar', {'has-error': invalid}]" role="table">
          <thead>
            <tr>
              <th v-for="(col, i) in columns" :key="i" :class="{comment: col.isComment, confirm: col.isConfirm, 'check-diff': col.isCheckDiffPrice}" align="center">
                {{ col.label }}
              </th>
            </tr>
          </thead>
          <tbody>
            <tr
              v-for="(datum, _index) in data" 
              :key="datum.cd ? datum.cd : _index">
              <td v-for="(column, j) in columns" :key="j" align="center">
                <template v-if="column.getData">
                  {{ column.getData(datum)}}
                </template>
                <template v-else-if="column.isInput">
                  <validation-provider v-slot="{invalid}" :rules="column.validateRule">
                    <template v-if="column.type == 'text'">
                      <b-form-input
                        :class="[column.class, {invalidClassTable: invalid}]"
                        type="text"
                        :name="column.field"
                        v-model="datum[column.field]"
                        :readonly="column.isReadOnly"
                        :disabled="column.disabled || disabled"
                        @input="column.onInput && column.onInput(datum, $event)"
                      ></b-form-input>
                    </template>
                    <template v-else-if="column.type == 'numeric'">
                      <v-numeric
                        :class="[
                          column.class, 
                          {'change-value' : column.initialField && (datum[column.initialField] != datum[column.field])},
                          {invalidClassTable: invalid}
                        ]"
                        type="number"
                        v-model="datum[column.field]"
                        :separator="column.separator ? column.separator : ','"
                        :min="column.min"
                        :max="column.max"
                        :step="column.step ? column.step : 1"
                        :precision="column.step == 0.01 ? 2 : 0"
                        :disabled="column.disabled || disabled"
                        @input="column.onInput && column.onInput(datum, $event)"
                    ></v-numeric>
                    </template>
                  </validation-provider>
                </template>
                <template v-else-if="column.isSelect">
                  <validation-provider v-slot="{invalid}" :rules="column.validateRule">
                    <v-select
                      :class="['main-vselect account-create', column.class, {invalidClassTable: invalid}]"
                      v-model="datum[column.field]"
                      :options="column.options"
                      :reduce="column.reduce"
                      :disabled="column.disabled || disabled"
                      @input="column.onInput && column.onInput(datum, $event)"
                      @open="column.onInput && column.onOpen(datum)"
                      @search="column.onSearch && column.onSearch($event)"
                      @close="column.onInput && column.onClose"
                    ></v-select>
                  </validation-provider>
                </template>
                <template v-else-if="column.isComment">
                  <button :class="['btn-icon', column.class]" @click="column.onClick(datum, _index, keyList)">
                    <i
                      :class="[
                        'fas', 
                        datum.hasComment ? 'fa-comment-dots': 'fa-comment-medical',
                        datum.hasDiff && !datum.hasComment ? 'color_red' : 'color_grey'
                      ]"
                    ></i>
                  </button>
                </template>
                <template v-else-if="column.isConfirm">
                  <button :class="['btn-icon', column.class]" @click="column.onClick(datum, keyList)">
                    <i :class="[
                      'fas fa-check-circle',
                      datum.isChecked
                        ? 'color_blue' 
                        : column.status && column.status(datum) == 'not-confirm'
                          ? 'color_red' : 'color_grey']"></i>
                  </button>
                </template>
                <template v-else-if="column.isCheckDiffPrice">
                  <button :class="['btn-icon', column.class]">
                    <i :class="['fas fa-exclamation', column.status && column.status(datum, columns)? 'color_red' : 'color_grey']"></i>
                  </button>
                </template>
                <template v-else>
                  {{datum[column.field]}}
                </template>
              </td>
            </tr>
          </tbody>
          <tfoot v-if="isShowFooter">
            <tr>
              <td :colspan="columns.length">小計: {{ footerData }}</td>
            </tr>
          </tfoot>
        </table>
    </b-container>
  </ValidationObserver>
</template>
<script>
export default {
  name: "DataTable",
  props: {
    keyList: {
      type: String,
      default: ""
    },
    columns: {
      type: Array,
      default: () => []
    },
    data: {
      type: Array,
      default: () => []
    },
    disabled: {
      type: Boolean,
      default: false
    },
    isShowAddItemIcon: {
      type: Boolean,
      default: true
    },
    isShowCopyItemIcon: {
      type: Boolean,
      default: true
    },
    isShowRemoveItemIcon: {
      type: Boolean,
      default: true
    },
    isShowFooter: {
      type: Boolean,
      default: true
    },
    footerData: {
      type: Number,
      default: 0
    }
  }
}
</script>
<style lang="scss" scoped>
.table-container {
  .table {
    overflow: auto;
    position: relative;
    display: block;
    width: 100%;
    padding-left: 0px;
    border-collapse: separate;
    border-spacing: 0px;
    transition: all .7s;
    th, td {
      text-align: center;
      white-space: nowrap;
      vertical-align: middle;
      font-size: 0.75rem;
    }
    thead > tr > th {
      background: grey;
      color: #fff;
      font-weight: 400;
      &:not(.confirm):not(.comment):not(.check-diff) {
        min-width: 5rem;
      }
    }
    tbody > tr > td {
      font-weight: 400;
      padding: 2px;
      color: #030303;
      i {
        font-size: 1.375rem !important;
      }
      input {
        font-size: 0.75rem;
        color: #030303 !important;
        border: 1px solid #707070;
        border-radius: 0;
        padding:5px;
        &:focus,
        &:active {
          outline:none;
        }
        &.change-value {
         background: #FCE655;
        }
      }
      .v-select {
        border: 1px solid #707070;
        /deep/ & > div {
          min-width: 5rem;
          padding: 0 5px;
          border: none;
          .vs__selected-options {
            height: 1.75rem;
          }
        }
      }
      .btn-icon {
        background: transparent;
      }
    }
    tfoot > tr > td {
      border-bottom: 1px solid #dee2e6;
    }
  }
}
</style>