<template>
  <validation-provider
          v-slot="{invalid, validated}"
          :rules="rules"
          :custom-messages="messages">
    <b-form-group :label="name" class="label_input" :class="{ invalidClass: invalid && validated}" :label-for="id">
      <date-picker
              :id="id"
              @input="onInput"
              :value="value"
              :class="['main-vselect',fieldClass]"
              :value-type="valueType"
              :disabled="disabledField"
              :format="formatDate"></date-picker>
    </b-form-group>

  </validation-provider>
</template>

<script>
  export default {
    name: "DateSskf",
    props: {
      id:{
        type: String,
        default:''
      },
      name:{
        type: String,
        default: ' ',
      },
      value: {
        type: [String, Number, Date],
        default: undefined
      },
      valueType: {
        type: String,
        default: 'YYYY-MM-DD'
      },
      formatDate: {
        type: String,
        default: 'YYYY-MM-DD'
      },
      disabledField: {
        type: Boolean,
        default: false
      },
      rules: {
        type: Object,
        default: undefined
      },
      fieldClass: {
        type: [Object, String],
        default: ''
      },
      changed:{
        type: Boolean,
        default: false
      },
      messages: {
        type: Object,
        default: undefined
      }
    },
    methods: {
      onInput(event) {
        this.$emit("input", event);
      },
    }
  };
</script>

