<template>
  <div v-if="showModel" class="normalProcess" :class="{processClass: isProcess}">
    <p v-html="title"></p>
  </div>
</template>

<script>
  export default {
    name: "NormalProcessingDialog",
    data() {
      return {
        showModel: this.value
      }
    },
    props: {
      title: {
        type: String,
        default: "Not Thing To Show"
      },
      value: {
        type: Boolean,
        default: false
      },
      isProcess:{
        type: Boolean,
        default: false
      }
    },
    watch: {
      show() {
        this.showModel = this.value;
      },
      showModel() {
        this.$emit('input', this.showModel)
      }
    },
    created() {
      setTimeout(() => {
        this.$emit('handleClose');
      },2000);
    }
  }
</script>

<style lang="scss" scoped>
  .normalProcess {
    position: fixed;
    width: 30%;
    height: 250px;
    top: 0;
    left: 30%;
    background-color: #f47272;
    border-radius: 0px 0px 15px 15px;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
    p {
      margin-left: 10%;
      margin-right: 10%;
      text-align: left;
      font: Bold 16px/16px Meiryo;
      letter-spacing: 0px;
      line-height: 2rem;
      font-size: 1.1rem;
      color: #FFFFFF;
      opacity: 1;
    }
  }
  .processClass{
    background: #707070 0% 0% no-repeat padding-box;
  }
</style>