<template>
  <b-container fluid class="no-gutters px-0 upload-zone">
    <b-row class="no-gutters">
      <b-col md="12" lg="6" class="attachFile_body p-0">
        <validation-provider
                :immediate="false"
                v-slot="{invalid,validated}"
                :rules="rules"
                :custom-messages="messages"
                slim
        >
          <input type="hidden" :value="fileSize" :class="{ invalidClass: invalid && validated}"/>
        </validation-provider>
        <b-form-group
                label=""
                label-for="files"
                class="label_input label_textarea pr-0">
          <b-form-file
                  id="upload-files"
                  ref="attachFile"
                  browse-text=" "
                  placeholder=" "
                  :disabled="disabled"
                  :accept="accept"
                  :multiple="multiple"
                  @input="setAttachFile"
          ></b-form-file>
        </b-form-group>
        <b-row no-gutters class="attachFile_body_1" align-h="between">
          <b-col cols="4"><label>ファイル添付</label></b-col>
          <b-col cols="3" class="attachFile_body_btn">
            <label for="upload-files" class="btn btn-none">
              <!-- <b-button
                variant="none"> -->
              ファイルを<br>選択する
              <!-- </b-button> -->
            </label>
          </b-col>
          <b-col cols="5" class="note" align-self="center">
            <label class="note">ここにファイルを<br>ドラッグ&ドロップ</label>
          </b-col>
        </b-row>
        <!-- </validation-provider> -->
      </b-col>
      <b-col md="12" lg="6" class="attachFile_filename">
        <div class="customScrollBar">
          <b-row class="mt-1 file-container" no-gutters v-for="(item, index) in files" :key="index">
            <b-col md="auto" class="px-3 file">
              <i class="fas fa-file"></i>
              <a href="#">{{item.name ? item.name : item.fileOriginalName}}</a>
            </b-col>
            <b-col md="auto" class="icon">
              <i class="fas fa-minus-circle" :disabled="disabled" @click="deleteAttachFile(index)"></i>
            </b-col>
          </b-row>
        </div>
      </b-col>
    </b-row>
  </b-container>
</template>
<script>
  export default {
    name: "UploadFiles",
    props: {
      files: {
        type: Array,
        default: () => []
      },
      multiple: {
        type: Boolean,
        default: true
      },
      disabled: {
        type: Boolean,
        default: false
      },
      accept: {
        type: String,
        default: () => {
          return "pdf, csv, jpg, jpeg, JPG, JPEG, jpe, jfif, pjpeg, pjp, png, gif, svg, svgz, tif, tiff, bmp, doc, docm, docx, dot, dotx, xls, xlsx, xdw";
        }
      },
      rules: {
        type: Object,
        default: undefined
      },
      messages: {
        type: Object,
        default: undefined
      }
    },
    computed: {
      fileSize() {
        if(!this.files || !Array.isArray(this.files)) {
          return 0;
        }
        let size = this.files.reduce((a, b) => {
          let size1 = a.size || 0,
              size2 = b.size || 0;
          console.log(size1 + size2, size)
          return size1 + size2;
        }, 0);
        return size || 0;
      }
    },
    methods: {
      setAttachFile(event) {
        for (let i = 0; i < event.length; i++) {
          this.$emit("set-attach-file-for-request", event[i]);
        }
      },
      deleteAttachFile(index) {
        this.$emit("remove-file", index);
      }
    }
  }
</script>
<style lang="scss" scoped>
  .upload-zone {
    margin-left: 0;
    & > div.row {
      height: 100%;
      /deep/ .attachFile_body {
        height: 7rem;
        max-height: 7rem;
        .attachFile_body_1 {
          height: 100%;
          .attachFile_body_btn {
            display: flex;
            align-items: center;
            label {
              &.btn {
                width: 7.5rem;
                height: 4.125rem;
                font-size: 1rem;
                padding: 8px 17.5px;
                background-color: #44a5cc;
                color: white;
                border: none;
                margin: 0.5rem 0;
              }
            }
          }
        }
        input[type="file"] {
          height: 100%;
        }
        input[type="hidden"].invalidClass {
          display: initial;
          & ~ .attachFile_body_1 > div:first-child > label{
            background: #df6561;
          }
        }
      }
      /deep/ .attachFile_filename > div {
        height: 12rem;
        max-height: 12rem;
        overflow: hidden auto;
        .file-container {
          display: flex;
          flex-wrap: nowrap;
          .file {
            display: flex;
            flex-wrap: nowrap;
            a {
              color: #44a5cc;
              font-size: 0.8rem;
              text-decoration: underline;
              margin: 0 .5rem;
              white-space: nowrap;
              overflow: hidden;
              text-overflow: ellipsis;
            }
          }
          i {
            font-size: inherit;
          }
        }
      }
    }
  }
</style>