<template>
  <b-modal v-model="showpopup" size="xl" centered hide-header hide-footer body-class="p-0">
    <b-container fluid class="searchproductPopup searchAdminPopup searchRequestPopup searchSupply p-0">
      <div class="bg_header px-5 pt-3">
        <div class="supplieName">取引先名検索</div>
        <b-row class="mt-3">
          <b-col cols="6">
            <b-form-group label="取引先略称" label-for="product_code" class="label_input searchEmployee searchproduct">
              <b-form-input type="text" v-model="dataSearch.torihikiRnmForSearch" id="product_code" @change='changeInput($event)'></b-form-input>
            </b-form-group>
          </b-col>
          <b-col cols="6">
            <b-form-group label="取引先略称カナ" label-for="product_classification" class="label_input searchEmployee searchproduct">
              <v-select v-model="dataSearch.torihikiRknmForSearch" id="product_classification"
                        class="table_requestList main-vselect bg-light"
                        @input='changeInput($event)'
                        @open="onOpenSelect"
                        :reduce="text => text.value" label="text"
                        :options="listPartner" />
            </b-form-group>
          </b-col>
        </b-row>

        <b-row class="mt-4">
          <b-col cols="8" align-self="end" class="searchSupplieBtn mb-3">
            <b-button-group>
              <b-button variant="none" @click="showpopup = false" class="btn-white">戻る</b-button>
              <b-button variant="none" @click="searchSupplier()" class="ml-3 btn-blue">検索する</b-button>
            </b-button-group>
          </b-col>
          <b-col cols="1" class="align_self">
            <p>表示件数</p>
            <b-form-select v-model="dataSearch.limitNumber" :options="litmit" @change='changeInput($event)' class="input_filter">
            </b-form-select>
          </b-col>
          <b-col cols="3" class="align_self">
            <p>表示順</p>
            <b-form-select v-model="dataSearch.sortField" :options="directions" @change='changeInput($event)' class="input_filter">
            </b-form-select>
          </b-col>
        </b-row>
      </div>

      <div class="px-5 py-3">
        <h4 class="px-5">取引先名称</h4>
        <div class="px-5" v-if="!dataSuplier || dataSuplier.content.length == 0">
          <hr class="m-0 hr" />
        </div>
        
        <b-row class="search_record m-0 mt-2 px-5 py-3">
          <b-table-simple v-if="dataSuplier">
            <b-tbody>
              <b-tr v-for="(item, index) in dataSuplier.content" :key="index" @dblclick="$emit('getSuplier', item)">
                <b-td>三菱食品（株）:</b-td>
                <b-td>{{item['torihiki1Nm']}}</b-td>
                <b-td>{{item['torihiki2Nm']}}</b-td>
              </b-tr>
            </b-tbody>
          </b-table-simple>
        </b-row>

        <div class="hight_pagination">
          <div v-if="dataSuplier && dataSuplier.content.length > 0">
            <b-pagination
                    v-model="pageCurrent"
                    :total-rows="dataSuplier.totalElements"
                    :per-page="dataSuplier.pageable.pageSize "
                    @change="handerNextPage($event)"
            >
              <template v-slot:first-text>
                <i class='fas fa-angle-double-left pagiClass'></i>
              </template>
              <template v-slot:prev-text>
                <i class='fas fa-angle-left pagiClass'></i>
              </template>
              <template v-slot:next-text>
                <i class='fas fa-angle-right pagiClass'></i>
              </template>
              <template v-slot:last-text>
                <i class='fas fa-angle-double-right pagiClass'></i>
              </template>
            </b-pagination>
          </div>
        </div>
      </div>
    </b-container>
  </b-modal>
</template>
<script>
  import SupplierService from "@/services/SupplierService";
  import modelApi from "@/services/ModelApi";
  export default {
    name: "SearchSupplyDialog",
    data() {
      return {
        showpopup: this.value,
        litmit: [
          {value: 20, text: '20'},
          {value: 50, text: '50'}
        ],
        directions: [
          {value: 'torihikiCdNk==ASC', text: '取引先名称(昇順)'},
          {value: 'torihikiCdNk==DESC', text: '取引先名称(降順)'}
        ],
        businessPartner: [],
        dataSearch: {
          torihikiRknmForSearch: '',
          torihikiRnmForSearch: '',
          pageNumber: 1,
          limitNumber: 20,
          sortField: 'torihikiCdNk==ASC'
        },
        dataSuplier: null,
        pageCurrent: 1
      };
    },
    props: {
      value: {
        type: Boolean
      }
    },
    methods: {
      async onOpenSelect(){
        if(!this.businessPartner.length){
          await SupplierService.listDropDownSuppliers()
              .then(res => {
                this.businessPartner = res.data;
              })
              .catch(err => {
                console.log(err);
              });
        }
      },
      handerNextPage(event){
        this.dataSearch.pageNumber = event;
        this.searchSupplier();
      },
      searchSupplier() {
        var keyword = '';
        if (this.dataSearch.torihikiRknmForSearch !== '' && this.dataSearch.torihikiRknmForSearch !== null) {
          keyword += "torihikiRknmForSearch=='" + this.dataSearch.torihikiRknmForSearch + "'";
        }

        if (this.dataSearch.torihikiRnmForSearch !== '' && this.dataSearch.torihikiRnmForSearch !== null) {
          if (keyword !== '') {
            keyword += ";";
          }

          keyword += "torihikiRnmForSearch=='" + this.dataSearch.torihikiRnmForSearch + "'";
        }
        
        var queryString = new modelApi.modelGet(keyword,this.dataSearch.sortField,this.dataSearch.limitNumber , this.dataSearch.pageNumber - 1).queryString();
        SupplierService.searchSuppliers(queryString).then(res => {
          this.dataSuplier = res.data;
          this.pageCurrent = res.data.number + 1;
        }).catch(err => {
          console.log(err);
        })
      },
      changeInput(text) {
        this.dataSearch.pageNumber = 1;
      }
    },
    watch: {
      value() {
        this.showpopup = this.value;
      },
      showpopup() {
        this.$emit("input", this.showpopup);
      }
    },
    computed: {
      listPartner: function () {
        var obj;
        obj = this.businessPartner.reduce((type, elm) => {
          var obj1 = [{value: elm, text: elm}];
          return [...type, ...obj1];
        }, []);
        return obj;
      },

    },
  };
</script>
<style lang="scss" scoped>
  p {
    margin: 0;
    font-size: 0.7rem;
    font-weight: 500;
  }

  .input_filter {
    width: 100%;
    font-size: 0.7rem;
    font-weight: 700;
    border: 1px solid grey;
    border-radius: 0;
    padding: 0 0.4rem;
  }

  .searchproductPopup {
    padding: 1vh 4vw;
  }
  .table th, .table td {
    border-top: 1px solid grey;
    font-size: 1rem;
  }
  table tr{
    cursor: pointer;
  }
  .searchRequestPopup {
    input {
      height: auto !important;
      border-radius: 0;
    }
  }
  .bg_header {
    background-color: #E5E5E5;
  }

  .hr {
    border-top: 1px solid grey;
  }
</style>

<style lang="scss">

.searchSupply {
  .searchproduct {
    background-color: transparent !important;
  }

  .searchproduct label {
    padding-left: 0 !important;
    width: auto !important;
    padding-right: 0.25rem !important;
  }

  .hight_pagination {
    height: 2rem;
  }

  .align_self {
    align-self: flex-end;
  }
}

</style>