<template>
  <b-modal v-model="isShowing" size="xl" centered hide-header hide-footer>
    <b-container fluid class="bv-example-row history-comment">
      <div class="m-3">
        <div class="popup-title">コメント候補</div>
        <b-row class="search_record mt-1" >
          <b-table-simple bordered borderless>
            <b-tbody>
              <b-tr v-for="(comment,index) in historyComments" :key="index" @dblclick="$emit('onSelectedComment', comment[0])" class="comment-item">
                <b-td><label class="comment-history-item">{{comment[0]}}</label></b-td>
              </b-tr>
            </b-tbody>
          </b-table-simple>
        </b-row>
        <b-row class="searchBtn" align-h="end">
            <b-button variant="none" @click="isShowing = false" class="btn-white">戻る</b-button>
        </b-row>
      </div>

    </b-container>
  </b-modal>
</template>
<script>
  import VueJwtDecode from 'vue-jwt-decode';
  import cookie from "vue-cookie";
  import CommentService from "@/services/CommentService";
  export default {
    name: "CommentHistoryDialog",
    data() {
      return {
        isShowing: this.value,
        historyComments: []
      };
    },
    props: {
      value: {
        type: Boolean
      }
    },
    methods: {
      getOldComments () {
        var decodeToken = VueJwtDecode.decode(cookie.get("accessToken"));
        let param = { shainCd: decodeToken.sub };
        CommentService.getOldComments(param).then(res => {
            this.historyComments = res.data;
          }).catch(err => {
            console.log(err);
          });
      }
    },
    computed:{

    },
    watch: {
      isShowing () {
        this.$emit("input", this.isShowing);
      }
    },
    created() {
      this.getOldComments();
    }
  };
</script>
<style lang="scss" scoped>
    .history-comment {
      .popup-title {
        font-size: 1.3rem;
      }
      .search_record{
        height: 44vh;
        overflow-y: auto;
        border: 1px solid grey;
      }

      table{
        border: none;
        tr, label{
          cursor: pointer;
        }
      }
      .searchBtn button {
        padding: 1rem 0;
        text-align: center;
        width: 8rem;
        margin-top: 1rem;
      }

      .comment-item {
        font-size: 1rem;
      }
      .comment-history-item {
        white-space: nowrap;
        width: 60rem;
        overflow: hidden;
        text-overflow: ellipsis;
        margin: 0;
      }
    }
</style>