import axios from "axios";
import TokenService from "@/services/TokenService"
import router from "@/router";
import store from "@/store";
const CancelToken = axios.CancelToken;
let cancel;

function handleToLoginPage() {
  TokenService.removeToken();
  store.dispatch('AuthenticationModule/setAccessToken');
  router.push("/login");
}

var http = axios.create({
  baseURL: window.location.origin + "/api",
  headers: {
    "Content-type": "application/json"
  }
});

http.interceptors.request.use(function (config) {
  let token = TokenService.getToken();
  config.cancelToken =  new CancelToken(function executor(c)
  {
    cancel = c;
  });
  if(!token && config.url !== "/authenticate") {
    handleToLoginPage();
    cancel();
  } else if (token && config.url !== "/authenticate") {
    config.headers["Authorization"] = "Bearer " + token;
    return config;
  } else {
    return config;
  }
}, function (error) {
  return Promise.reject(error);
});

http.interceptors.response.use((response) => {
  return response;
}, function (error) {
  window.console.error(error);
  const originalRequest = error.config;
  if(error.response && error.response.status === 401 && 
    (originalRequest.url.includes("/authenticate") || 
    originalRequest.url.includes("/refresh-token"))
  ) {
    handleToLoginPage();
    return Promise.reject(error);
  }
  if (error.response && error.response.status === 401 && !originalRequest._retry) {
    originalRequest._retry = true;
    let refreshToken = TokenService.getRefreshToken();
    if (refreshToken) {
      return http.post("/refresh-token", {
        "refreshToken": refreshToken
        })
        .then(res => {
          if(res.status === 201) {
            TokenService.saveToken(res.data.accessToken);
            TokenService.saveRefreshToken(res.data.refreshToken);
            // Change Authorization header
            http.defaults.headers.common["Authorization"] = "Bearer " + TokenService.getToken();
            // Call originalRequest object with Axios.
            return http(originalRequest);
          }
        });
    } else {
      handleToLoginPage();
      return Promise.reject(error);
    }
  }
  // return Error object with Promise
  return Promise.reject(error);
});
export default http;
