const isTablet = () => {
  const useragent = window.navigator.userAgent
  const platform = window.navigator.platform.toLowerCase();
  const tabletPlatform = /(ipados|ipad|tablet|(android(?!.*mobile))|(windows(?!.*phone)(.*touch))|kindle|playbook|silk|(puffin(?!.*(IP|AP|WP))))/
  if(tabletPlatform.test(platform)){
    return true
  }else {
    if(tabletPlatform.test(useragent)){
      return true
    }else {
      return false
    }
  }

};

export default isTablet;