<template>
  <b-modal v-model='showPopupSearch' size='xl' centered hide-header hide-footer>
    <b-container fluid class='searchproductPopup searchAdminPopup searchRequestPopup searchRequestPopupModify'>
      
        <div class='supplieName'>検索条件</div>
        <b-row no-gutters>
          <!--21-->
          <b-col cols='6'>
            <b-form-group label='申請番号' label-for='apply_number' class='label_input searchEmployee searchproduct mt-3'>
              <b-form-input v-model='filterRequest.applyNumber' type='text' id='apply_number'></b-form-input>
            </b-form-group>
          </b-col>
          <!--22-->
          <b-col cols='6'>
            <b-form-group label='決済番号' label-for='settlement_number' class='label_input searchEmployee searchproduct mt-3'>
              <b-form-input v-model='filterRequest.settlementNumber' type='text' id='settlement_number'></b-form-input>
            </b-form-group>
          </b-col>
          <!--23-->
          <b-col cols='6'>
            <b-form-group label='請求タイプ' label-for='request_type' class='label_input searchEmployee searchproduct mt-3'>
              <v-select
                id='request_type'
                class='main-vselect'
                v-model='filterRequest.requestType'
                :options='mstRequestTypes'
                :reduce='mstRequestType => mstRequestType.name'
                label='name'
              >
              </v-select>
            </b-form-group>
          </b-col>
          <!--24 display screen filter all-->
          <b-col cols='6' v-if='tabActive === 1'>
            <b-form-group label='関係者' label-for='related_person' class='label_input searchEmployee searchproduct mt-3'>
              <b-form-input v-model='filterRequest.relatedPerson' type='text' id='related_person'></b-form-input>
            </b-form-group>
          </b-col>
          <!--25 diplay when screen filter in-process or all or send-back or reject-->
          <b-col cols='6' v-if='tabActive === 0 || tabActive === 1 || tabActive === 2 || tabActive === 3'>
            <b-form-group label='確認者' label-for='confirm_person' class='label_input searchEmployee searchproduct mt-3'>
              <b-form-input v-model='filterRequest.confirmPerson' type='text' id='confirm_person'></b-form-input>
            </b-form-group>
          </b-col>
          <!--26 diplay when screen filter in-process or all or send-back or reject-->
          <b-col cols='6' v-if='tabActive === 0 || tabActive === 1 || tabActive === 2 || tabActive === 3'>
            <b-form-group label='申請者' label-for='applicant' class='label_input searchEmployee searchproduct mt-3'>
              <b-form-input v-model='filterRequest.applicant' class="mainHeigh-2" type='text' id='applicant'></b-form-input>
            </b-form-group>
          </b-col>
          <!--27 diplay when screen filter in-process or all or send-back or reject-->
          <b-col cols='6' v-if='tabActive === 0 || tabActive === 1 || tabActive === 2 || tabActive === 3'>
            <b-form-group label='承認者' label-for='approver' class='label_input searchEmployee searchproduct mt-3'>
              <b-form-input v-model='filterRequest.approver' type='text' id='approver'></b-form-input>
            </b-form-group>
          </b-col>
          <!--28 diplay when screen filter in-process or all-->
          <b-col cols='6' v-if='tabActive === 0 || tabActive === 1'>
            <b-form-group label='ステータス' label-for='status' class='label_input searchEmployee searchproduct mt-3'>
              <v-select
                id='status'
                class='main-vselect'
                v-model='filterRequest.status'
                :options='mstRequestStatuses'
                :reduce='mstRequestStatuse => mstRequestStatuse.name'
                label='name'
              >
              </v-select>
            </b-form-group>
          </b-col>
          <!--29-->
          <b-col cols='6'>
            <b-form-group label='取引先名' label-for='supplier_name' class='label_input searchEmployee searchproduct mt-3'>
              <b-form-input v-model='filterRequest.supplierName' type='text' id='supplier_name'></b-form-input>
            </b-form-group>
          </b-col>
          <!--30-->
          <b-col cols='12'>
            <b-row>
              <b-col cols='6' class="pr-0">
                <b-form-group label='申請日' label-for='appliedAtMin_appliedAtMax' class='label_input searchEmployee searchproduct mt-3'>
                  <date-picker v-model='filterRequest.appliedAtMin' value-type='YYYY/MM/DD' format='YYYY/MM/DD'></date-picker>
                </b-form-group>
              </b-col>
              <b-col cols='6' class="px-0">
                <b-form-group class='label_input searchEmployee searchproduct mt-3'>
                  <span class='mx-2'>~</span>
                  <date-picker v-model='filterRequest.appliedAtMax' value-type='YYYY/MM/DD' format='YYYY/MM/DD'></date-picker>
                </b-form-group>
              </b-col>
            </b-row>
          </b-col>

          <!--31-->
          <b-col cols='12'>
            <b-row>
              <b-col cols='6' class="pr-0">
                <b-form-group label='支払予定日' label-for='paymentScheduledDateMin_paymentScheduledDateMax' class='label_input searchEmployee searchproduct mt-3'>
                  <date-picker v-model='filterRequest.paymentScheduledDateMin' value-type='YYYY/MM/DD' format='YYYY/MM/DD'></date-picker>
                </b-form-group>
              </b-col>
              <b-col cols='6' class="px-0">
                <b-form-group class='label_input searchEmployee searchproduct mt-3'>
                  <span class='mx-2'>~</span>
                  <date-picker v-model='filterRequest.paymentScheduledDateMax' value-type='YYYY/MM/DD' format='YYYY/MM/DD'></date-picker>
                </b-form-group>
              </b-col>
            </b-row>
          </b-col>

          <!--32-->
          <b-col cols='6'>
            <b-form-group label='停滞日数' label-for='number_of_stagnancyDay' class='label_input searchEmployee searchproduct mt-3'>
              <b-form-input v-model='filterRequest.numberOfStagnancyDay' type='text' id='number_of_stagnancyDay'></b-form-input>
            </b-form-group>
          </b-col>

          <!--33-->
          <b-col cols='12'>
            <b-row>
              <b-col cols='6' class="pr-0">
                <b-form-group label='金額' label-for='amountMin_amountMax' class='label_input searchEmployee searchproduct mt-3'>
                  <b-form-input v-model='filterRequest.amountMin' type='text'></b-form-input>
                </b-form-group>
              </b-col>
              <b-col cols='6' class="px-0">
                <b-form-group class='label_input searchEmployee searchproduct mt-3'>
                  <span class='mx-2'>~</span>
                  <b-form-input v-model='filterRequest.amountMax' type='text'></b-form-input>
                </b-form-group>
              </b-col>
            </b-row>
          </b-col>
        </b-row>

        <div class='controller p-0 mt-3'>
          <div>
            <b-button @click='onReset' variant='none' class='btn-white mr-5'>リセット</b-button>
            <b-button @click='onSearch' variant='none' class='btn-blue create_new'>検索</b-button>
          </div>
        </div>
     
    </b-container>
  </b-modal>
</template>

<script>
import RequestService from '@/services/RequestService';
import FilterRequest from '@/models/FilterRequest';

export default {
  data() {
    return {
      mstRequestTypes: [],
      mstRequestStatuses: []
    }
  },
  props: [ 'isSearch', 'tabActive', 'filterRequest' ],
  computed: {
    showPopupSearch: {
      get: function () {
        return this.isSearch;
      },
      set: function (newValue) {
        this.$emit('set-is-search', newValue);
      }
    }
  },
  methods: {
    onReset() {
      this.$emit('set-filter-request-on-reset', new FilterRequest());
    },
    onSearch() {
      this.$emit('set-filter-request', this.filterRequest);
    },
    getFilterSelect() {
      RequestService.getFilterSelect()
        .then(res => {
          if (res.data) {
            this.mstRequestTypes = res.data.mstRequestTypesResponses;
            this.mstRequestStatuses = res.data.mstRequestStatusesResponses;

            this.mstRequestStatuses.push({
              'cd': 0,
              'name': '差し戻し' ///Send back
            });
          }
        })
        .catch(err => {
          alert('getRequestRearch: ' + err);
        });
    }
  },
  created() {
    this.getFilterSelect();
  }
}
</script>

<style lang='scss' scoped>
</style>