<template>
  <b-container fluid class="bv-example-row p-0 main">
    <b-row no-gutters class="main-header" align-h="between">
      <b-row cols="auto" align-v="center">
        <b-col cols="auto" @click="activeNum = 1" :class="{active_tablet: activeNum == 1 && isTabletDevice}"
        ><i v-if="isLogged && isTabletDevice" class="fas fa-home"></i>
          <p class="my-0">
            未収金<br v-if="!isTabletDevice"/>
            処理システム
          </p></b-col>
        <b-col v-if="isTabletDevice" class="main-nav_function tabletNav" cols="auto" @click="activeNum = 7"
               :class="{active_tablet: activeNum == 7}">
          承認する <span class="main-nav_count">{{ countRequest.waitingApprove }}</span>
        </b-col>
      </b-row>
      <b-col cols="auto" align-v="center" class="dropdownUser">
        <b-dropdown no-caret variant="none" v-if="isLogged">
          <template v-slot:button-content>
            <i class="fas fa-user iconUser"></i>
            <span class="mx-4">画面名機</span>
            <i class="fas fa-angle-down"></i>
          </template>
          <b-dropdown-item @click="$router.push('/user/edit')">ユーザ情報を見る</b-dropdown-item>
          <b-dropdown-item @click="$router.push('/master/menu')">マスタメニューを見る</b-dropdown-item>
          <b-dropdown-item href="#" @click="logout()">ﾛｸﾞｱｳﾄする</b-dropdown-item>
        </b-dropdown>
      </b-col>
    </b-row>
    <div class="main-body" :class="{ collapseNav: hascollpase }">
      <div class="main-nav" v-if="isLogged && !isTabletDevice">
        <router-link to="/" tag="div" :class="{activeNav : $route.path == '/' }"
                     class="homeNav p-0 text-center">
          <div><i class="fas fa-home"></i></div>
          <p class="my-0">ホーム</p>
        </router-link>
        <div
                class="request"
                :class="{activeNav : getParentRoutePath() == '/request' }"
        >
          <i class="fas fa-money-bill-wave"></i>請求
        </div>
        <router-link to="/request/list" tag="div" active-class="active_function" class="main-nav_function">
          申請一覧
        </router-link>

        <router-link to="/request/create" tag="div" active-class="active_function" class="main-nav_function">
          作成する<span class="main-nav_count">{{ countRequest.create }}</span>
        </router-link>
        <router-link to="/request/confirm" tag="div" active-class="active_function" class="main-nav_function">
          確認する <span class="main-nav_count">{{ countRequest.waitingConfirm }}</span>
        </router-link>
        <router-link to="/request/apply" tag="div" active-class="active_function" class="main-nav_function">
          申請する <span class="main-nav_count">{{ countRequest.waitingApply }}</span>
        </router-link>
        <router-link to="/request/approve" tag="div" active-class="active_function" class="main-nav_function">
          承認する <span class="main-nav_count">{{ countRequest.waitingApprove }}</span>
        </router-link>
        <button
                type="button"
                class="collapsebtn"
                @click="hascollpase = !hascollpase"
        >
          <i v-if="!hascollpase" class="fas fa-arrow-left"></i>
          <i v-if="hascollpase" class="fas fa-arrow-right"></i>
        </button>
      </div>
      <div class="main-content">
        <slot/>
      </div>
    </div>
  </b-container>
</template>

<script>
  import isTablet from "@/helper/isTabletDevice";
  import {mapState, mapGetters, mapActions} from "vuex";

  export default {
    name: "LayoutSskf",
    data() {
      return {
        hascollpase: false,
        activeNum: 1,
      };
    },
    methods: {
      ...mapActions('AuthenticationModule', ['logout']),
      ...mapActions({
        countRequestList: "CountRequestModule/countRequestList"
      }),
      getParentRoutePath(){
        if(!this.$route.matched[0]){
          return ''
        }
        return this.$route.matched[0].path
      }
    },
    computed: {
      isTabletDevice() {
        return isTablet();
      },
      ...mapState({
        countRequest: state => state.CountRequestModule.countRequest
      }),
      ...mapGetters({
        isLoggedGetter: "AuthenticationModule/isLogged"
      }),
      isLogged(){
        return this.$route.name !== 'login' && this.isLoggedGetter;
      }
    },
    created() {

    },
    updated() {
      if(this.isLogged) {
        this.countRequestList();
      }
    },
  };
</script>

<style lang="scss" scoped>
  .main {
    min-width: 100vw !important;
    min-height: 100vh !important;
    overflow: auto;
  }
</style>
