<template>
  <div class='main_request_display_body body_padding_inputting_content customScrollBar'>
    <b-container fluid class='m-0 pt-4'>
      <div v-for='(item, index) in requestList' v-bind:key='index' class='request_item'>
        <request-item
          v-if='!updateList'
          v-bind:item='item'
        >
        </request-item>
      </div>
    </b-container>
  </div>
</template>

<script>
import RequestService from '@/services/RequestService';

export default {
  name: 'RequestDisplay',
  data() {
    return {};
  },
  props: ['requestList', 'isUpdateList'],
  methods: {
    setIsUpdateList(value) {
      this.$emit('set-is-update-list', value);
    }
  },
  computed: {
    updateList() {
      this.setIsUpdateList(false);
      return this.isUpdateList;
    }
  }
};
</script>

<style lang='scss'>
.main_request_display_body {
  height: 50vh;
  overflow-y: auto;
  align-self: flex-end;

  .request_item:last-of-type {
    .requestListItem {
      border-bottom: 2px solid lightgrey;
    }
  }
}
</style>